<script>
	export default {
		onLoad() {
			this.userinfo = uni.getStorageSync('userinfo')
			if(this.userinfo.role == 0){
				this.roleflag = true
			}else{
				this.roleflag = false
			}
		},
		onShow() {
			//用于查询没查看的归还情况
			//this.querycount()
		},
		data() {
			return {
				userinfo:{},
				dongtaicount: 0,
				roleflag:false
			}
		},
		methods: {
			//查询归还状态
			querycount() {
				let that = this
				let queryobj = {
					status: 1,
					readflag: 0
				}
				uni.showLoading({
					title: '加载中',
					mask: true,
				});
				uniCloud.callFunction({
					name: 'select_usergoods_readcount',
					data: queryobj,
					success(res) {
						uni.hideLoading();
						let arr = res.result.data.data
						console.log(arr)
						if(arr.length == 0){
							that.dongtaicount = 0
							return true
						}
						for (let i = 0; i < arr.length; i++) {
							if (arr[i]._id == 0) {
								console.log(arr[i])
								that.dongtaicount = arr[i].num
								console.log(that.dongtaicount)
							}
						}
					},
					fail() {
						uni.hideLoading();
						uni.showToast({
							title: '操作失败',
							icon: 'none',
							duration: 2000
						});
					},
					complete() {}
				});

			},
			fun01(){
				uni.navigateTo({
					url: '/pages/admin/activitylist'
				});
			},
			fun02(){
				uni.navigateTo({
					url: '/pages/admin/activitybaoming'
				});
			},
			fun03(){
				uni.navigateTo({
					url: '/pages/admin/shetuanshenpi'
				});
			},
			fun04(){
				uni.navigateTo({
					url: '/pages/admin/newslist'
				});
			},
			fun05(){
				uni.navigateTo({
