				});
			},
			fun08(){
				uni.navigateTo({
					url: '/pages/admin/usercenter'
				});
			},
			fun09(){
				uni.navigateTo({
					url: '/pages/admin/shentuanuserlist'
				});
			},
			//论坛
			fun10(){
				uni.navigateTo({
					url: '/pages/admin/luntanlist'
				});
			},
			fun11(){
				uni.navigateTo({
					url: '/pages/admin/fankuilist'
				});
			},
			todaka() {
				uni.navigateTo({
					url: '/pages/daka/daka'
				});
			},
			zujie() {
				uni.navigateTo({
					url: '/pages/zujie/zujie'
				});
			},
			zujielist() {
				uni.navigateTo({
					url: '/pages/zujie/zujielist'
				});
			},
			dakalist() {
